import { hardwareApi } from "../api";
import router from "../router";
import time from "./time";
import store from "../store";
let cardOptionData = {};
let CardReader5018Ins;

let showAlertDialog = (msg) => {
  store._actions.showAlertDialog[0](msg);
};
let hideReadCardLoaderDialog = () => {
  store._actions.hideReadCardLoaderDialog[0]();
};
let showReadCardLoaderDialog = () => {
  store._actions.showReadCardLoaderDialog[0]();
};

class base {
  constructor() {
    this.getMacPath = "/SmartTerminal/GetMacVerify";
    this.isValidation = false;
    this.mac = "";
    this.p;
    this.promiseFindCard_state;
    this.timeSpan = 400
  }
  getWsIns() {
    return (
      (this.wsIns =
        this.wsIns && (1 == this.wsIns.readyState || 0 == this.wsIns.readyState)
          ? this.wsIns
          : new WebSocket(window.config.webSocketUrl)),
      this.wsIns
    );
  }
  async wsPromise(sendData, alertDialogCallBack = null) {
    let ws = this.getWsIns();
    const _this = this;
    return new Promise(function (resolve, reject) {
      ws.onmessage = function (d) {
        d = JSON.parse(d.data);
        switch (d.result) {
          case "succ":
            console.log(`助手返回：${JSON.stringify(d)}`)
            return resolve(d);
          case "noCard":
            if (sendData.cmd == "findCard") return reject(d);
            else {
              showAlertDialog({ msg: "没有卡片！", successEvent: alertDialogCallBack });
            }
            break
          case "timeout":
            break
          case "fail":
            showAlertDialog({ msg: d.message, successEvent: alertDialogCallBack });
            break
          case "disconnected":
            showAlertDialog({
              msg: "读卡器连接断开,请检查连接并重新初始化", successEvent() {
                _this.wsPromise({
                  "cmd": "init",
                  "seq": "123"
                }).then(() => {
                  _this.wsPromise(sendData, alertDialogCallBack).then(d => {
                    resolve(d)
                  }).catch(e => {
                    console.log(JSON.stringify(e) + ",正在重新连接")
                    _this.wsPromise(sendData, alertDialogCallBack).finally(d => {
                      resolve(d)
                    })
                  })
                })
              }
            });
            break
          default:
            break;
        }
      };
      ws.onerror = function (e) {
        showAlertDialog({
          msg: "硬件助手连接已关闭或者没有链接成功,10秒后重新连接或检查助手端口", successEvent: (e) => {
            if (!e) {
              router.replace("/home")
              setTimeout(() => {
                window.location.reload()
              }, 0);
            }
          }
        });
        console.log("硬件助手连接失败：" + JSON.stringify(e))
      };
      ws.onclose = function () {
        console.log("硬件助手连接失败,正在重新连接")
        _this.wsPromise(sendData, alertDialogCallBack).then(d => {
          resolve(d)
        }).catch(e => {
          console.log(JSON.stringify(e) + ",正在重新连接")
          _this.wsPromise(sendData, alertDialogCallBack).then(d => {
            resolve(d)
          })
        })
      }
      ws.onopen = function () {
        ws.send(JSON.stringify(sendData));
      };
      if (ws.readyState == 1) {
        ws.send(JSON.stringify(sendData));
      }
    })
  }
  async verifyMAC(random) {
    return await new Promise((resolve) => {
      const _this = this;

      const _data = {
        random: random,
      };
      hardwareApi.GetMacVerify(_data).then((apiRet) => {
        if (apiRet && apiRet.rst != 1) {
          showAlertDialog(apiRet.Info);
          return;
        }
        _this.mac = apiRet.sJson.md5;
        const sendData = {
          seq: "123",
          Device: _this.Device,
          cmd: "verifyMAC",
          MAC: _this.mac,
        };
        _this.wsPromise(sendData).then((d) => {
          resolve(d);
        });
      });
    });
  }
  async getRandom() {
    const _this = this;
    const sendData = { seq: "123", Device: _this.Device, cmd: "getRandom" };
    const ret = await _this.wsPromise(sendData);
    return ret.random;
  }
}

class CardReader5018 extends base {
  setCardIniParamPromise(devType) {
    const _this = this;
    let p = new Promise(function (resolve) {
      _this.getRandomPromise(devType).then(function (d) {
        const _data = {
          random: d.random,
          TrmID: _this.TrmID,
          enterprise_id: _this.EnterpriseID,
        };
        hardwareApi.GetMacVerify(_data, (d) => {
          if (d.retCode == 1) {
            _this.validationMac(d.md5, devType).then(function (d1) {
              resolve(d1);
              _this.isValidation = true;
            });
          } else {
            // alert(d.retMsg || "获取参数失败");
          }
        });
      });
    });
    return p;
  }
  //设置参数
  async setParam(random) {
    const _this = this;
    const data = {
      random,
      termId: _this.TrmID,
      enterprise_id: _this.EnterpriseID,
    };
    let cardOption = await hardwareApi.GetCardIniParam(data);
    if (cardOption.rst == 1) {
      cardOptionData = cardOption.sJson;
      // cardOptionData.subsidyUnclean =
      //   cardOption.Data.clearBuZhu + "" === "true" ? 0 : 1;
      cardOptionData.cmd = "setParam";
      //delete cardOptionData.clearBuZhu;

      return await _this.wsPromise(cardOptionData);
    } else {
      showAlertDialog(cardOption.retMsg || "获取读卡参数失败！");
    }
  }
  delaly(duration) {
    return new Promise((resolve) => setTimeout(resolve, duration));
  }
  async promiseFindCard(timeout) {
    showReadCardLoaderDialog();
    const _this = this;
    _this.promiseFindCard_state = true;
    let id;
    if (timeout) {
      id = setTimeout(() => {
        _this.promiseFindCard_state = false;
      }, timeout * 1000);
    }
    while (_this.promiseFindCard_state) {
      let result = await _this.findCard();
      if (result && result.result == "succ") {
        _this.promiseFindCard_state = false;
        clearTimeout(id);
        hideReadCardLoaderDialog();
        return result;
      }
      await _this.delaly(_this.timeSpan);
    }
  }
  async promiseFindCard_silence(callBack) {
    const _this = this;

    if (router.currentRoute.path == "/home") {
      let result = await _this.findCard(async function () {
        return await _this.promiseFindCard_silence(callBack)
      });
      if (result && result.result == "succ") {
        hideReadCardLoaderDialog();
        callBack && callBack(result)
        return result;
      }
      await _this.delaly(_this.timeSpan);
      return await _this.promiseFindCard_silence(callBack)
    }
  }
  async findCard(alertDialogCallBack) {
    //const _this = this;
    const data = { seq: "123", cmd: "findCard" };
    try {
      let data2 = await this.wsPromise(data, alertDialogCallBack);
      return data2;
    } catch (error) {
      return 0;
    }
  }
  async checkCard(noCardDoit, timeSpan) {
    if (!router.currentRoute.path.match(/\/enterPwd/)) {
      return;
    }
    let findCardRet = await this.findCard();
    if (findCardRet && findCardRet.cardSN != store.state.curCardInfo.cardSN) {
      router.replace("/home")
      return
    }
    if (noCardDoit && !findCardRet && router.currentRoute.path.match(/\/enterPwd/)) {
      router.replace("/home")
      return
    }

    await time.delaly(timeSpan || this.timeSpan)
    this.checkCard(noCardDoit, timeSpan)
  }
  async readCard(cardSN) {
    const _this = this;
    const data = { cmd: "readCard", cardSN };
    const readCardRet = await _this.wsPromise(data);
    return readCardRet;
  }
  //脱机水卡钱包查询
  async SKQBGetValue(cardSN, alertDialogCallBack) {
    const _this = this;

    //判断卡片
    await _this.isCurCard(alertDialogCallBack)

    const data = {
      seq: 123,
      cmd: "SKQBGetValue",
      cardSN,
      key: cardOptionData.key,
      sector: cardOptionData.waterSector
    };
    const readCardRet = await _this.wsPromise(data, alertDialogCallBack);
    return readCardRet;
  }
  //第三方脱机水卡钱包查询
  async thirdWaterGetValue(cardSN, alertDialogCallBack) {
    const _this = this;

    //判断卡片
    await _this.isCurCard(alertDialogCallBack)

    const data = {
      seq: 123,
      cmd: "thirdWaterGetValue",
      cardSN
    };
    const readCardRet = await _this.wsPromise(data, alertDialogCallBack);
    return readCardRet;

  }
  async isCurCard(alertDialogCallBack) {
    //判断卡片
    let _this = this;
    let findcarInfo = await _this.promiseFindCard()

    if (findcarInfo.cardSN != store.state.curCardInfo.cardSN) {
      showAlertDialog({
        msg: "非当前操作卡片！",
        successEvent() {
          alertDialogCallBack && alertDialogCallBack()
        }
      })
      throw "非当前操作卡片！"
    }
  }
  //脱机水卡钱包加款
  async SKQBAddValue({ cardSN, oldValue, oldExTimes, opValue }, alertDialogCallBack) {
    const _this = this;

    await _this.isCurCard(alertDialogCallBack)

    const data = {
      seq: 123,
      cmd: "SKQBAddValue",
      cardSN,
      key: cardOptionData.key,
      sector: cardOptionData.waterSector,
      oldValue,//原卡金额 用于判断
      oldExTimes,//原卡脱机写卡次数(消费次数)
      opValue
    };
    const readCardRet = await _this.wsPromise(data, alertDialogCallBack);
    return readCardRet;
  }
  //第三方脱机水卡钱包加款
  async thirdWaterAddValue({ cardSN, oldLeftValue, oldTotalValue, opValue }, alertDialogCallBack) {
    const _this = this;

    //判断卡片
    await _this.isCurCard(alertDialogCallBack)

    const data = {
      seq: 123,
      cmd: "thirdWaterAddValue",
      cardSN,
      oldLeftValue,//原卡金额 用于判断
      oldTotalValue,//原卡脱机写卡次数(消费次数)
      opValue
    };
    const readCardRet = await _this.wsPromise(data, alertDialogCallBack);
    return readCardRet;
  }
  //第三方脱机水卡钱包设置参数
  async thirdWaterSetParam({ autoOpenCard }, alertDialogCallBack) {
    const _this = this;
    await _this.isCurCard(alertDialogCallBack)

    const data = {
      seq: 123,
      cmd: "thirdWaterSetParam",
      key: cardOptionData.key,
      whiteKey: "FFFFFFFFFFFF",
      sector: cardOptionData.thirdWaterSector,
      cardFormat: cardOptionData.thirdWaterCardFormat,
      autoOpenCard,
    };
    const readCardRet = await _this.wsPromise(data, alertDialogCallBack);
    return readCardRet;
  }
  async init() {
    const _this = this;
    const random = await _this.getRandom();
    await _this.verifyMAC(random);
    await _this.setParam(random);
  }
}

export default {
  startCardReader5018: async function (TrmID, EnterpriseID) {
    CardReader5018Ins = CardReader5018Ins || new CardReader5018();
    (CardReader5018Ins.TrmID = TrmID),
      (CardReader5018Ins.EnterpriseID = EnterpriseID);
    await CardReader5018Ins.init();
    return CardReader5018Ins;
  },
};
