import cry from "crypto";
import { MD5 } from "crypto-js";
export default {
  encryptDes: function(input, key, iv) {
    var cipher = cry.createCipheriv("des", new Buffer(key), new Buffer(iv));
    var buf1 = cipher.update(input, "utf8");
    var buf2 = cipher.final();
    var result = new Buffer(buf1.length + buf2.length);
    buf1.copy(result);
    buf2.copy(result, buf1.length);
    return result.toString("hex").toUpperCase();
  },
  decryptDes: function(encrypt_text, iv, key) {
    key = new Buffer(key);
    iv = new Buffer(iv ? iv : 0);
    var decipher = cry.createDecipheriv("des", key, iv);
    decipher.setAutoPadding(true);
    var txt = decipher.update(encrypt_text, "hex", "utf8");
    txt += decipher.final("utf8");
    return txt;
  },

  Str2Bytes: function(str) {
    var bytes = new Array();
    for (var i = 0; i < str.length; i++) {
      var s = str.substr(i, 1);
      var v = s.toString().charCodeAt();
      bytes.push(v);
    }
    return bytes;
  },
  ydDecryptDes: function(decrypt_text, key) {
    key = this.Str2Bytes(
      MD5(key.slice(0, 8))
        .toString()
        .toUpperCase()
    ).slice(0, 8);
    return this.decryptDes(decrypt_text, key, key);
  },
  ydencryptDes: function(encrypt_text, key) {
    key = this.Str2Bytes(
      MD5(key.slice(0, 8))
        .toString()
        .toUpperCase()
    ).slice(0, 8);
    return this.encryptDes(encrypt_text, key, key);
  }
};
