let getStackTrace = function () {
    let obj = {};
    window.Error.captureStackTrace(obj, getStackTrace);
    return obj.stack;
};
let PrefixZero = function (num, n) {
    //超长情况下取最字符串长度补两个空格
    if (num + "".length > n) {
        n = num.length + 2;
    }
    return (num + Array(n).join(' ')).slice(0, n);
}
let log = window.oldLog = console.log;
console.log = function () {
    let stack = getStackTrace() || ""
    let matchResult = stack.match(/\(.*?\)/g) || []
    let line = matchResult[1] || ""
    if (arguments.length == 0) return
    let i = arguments.length - 1
    log(arguments[i])
    if (typeof arguments[i] == 'object') {
        arguments[i] = JSON.stringify(arguments[i])
    }
    arguments[i] = PrefixZero(arguments[i], 500);
    arguments[i] += line.replace("(", "").replace(")", "")
    let str = arguments[i].trim();
    if (typeof (str) == "object") {
        str = JSON.stringify(str);
    }
    window.plus && window.plus.others.writeLogSys(str, 1);
}