export default {
  // 倒计时返回首页
  setTimeOutBackSpan(state, d) {
    state.TimeOutBackSpan = d
  },
  setTimeOutBackSpanInit(state) {
    if (!state.NewTimeOutBackSpan) state.TimeOutBackSpan = window.config.TimeOutBackSpan
  },
  showAlertDialog(state, d) {
    if (typeof d == "string") {
      state.alertDialog = {
        state: true,
        msg: d,
      };
    } else {
      state.alertDialog = {
        state: true,
        msg: d.msg,
        successEvent: d.successEvent,
      };
    }
  },
  hideAlertDialog(state, d) {
    state.alertDialog.successEvent && state.alertDialog.successEvent(d);
    state.alertDialog = {
      state: false,
      msg: "",
      successEvent: null,
    };
  },
  updateMenuList(state, d) {
    state.menuList = d;
  },
  showChildMenuList(state, d) {
    state.menuList = d;
  },
  SetToken(state, token) {
    state.token = token;
  },
  hideReadCardLoaderDialog(state) {
    state.readCardLoaderDialog = false;
  },
  showReadCardLoaderDialog(state) {
    state.readCardLoaderDialog = true;
  },
  setCurCardInfo(state, d) {
    state.curCardInfo = d;
  },
  switchLoading(state, d) {
    state.loading.state = d.state;
    state.loading.text = d.text;
    state.loading.color = d.color;
  },
  showConfirmDialog(state, { msg, successEvent, cancelEvent }) {
    state.confirmDialog.state = true
    state.confirmDialog.msg = msg
    state.confirmDialog.successEvent = successEvent
    state.confirmDialog.cancelEvent = cancelEvent
  },
  setConfirmDialog(state, d) {
    state.confirmDialog = d
  },
  setCurIsNeedIdentity(state, d) {
    state.curIsNeedIdentity = d;
  },
  setReader5018Ins(state, d) {
    state.Reader5018Ins = d;
  },
  setUserInfo(state, d) {
    state.userInfo = d
  },
  setConfiged(state, d) {
    state.configed = d
  },
  setPlatform(state, d) {
    state.Platform = d
  },
  setUUID(state, d) {
    state.UUID = d
  },
  setCefSharpState(state, d) {
    state.CefSharpState = d
  },
  setDebug(state, d) {
    state.debug = d
  },
  setNewTimeOutBackSpan(state, d) {
    state.NewTimeOutBackSpan = d
    if (d) state.TimeOutBackSpan = d
  }
};
