export const initPluginApp = () => {
    var _DevCODE = 'devapi_uniwin';
    var _BARCODE = 'pluginApp',
        B = window.plus.bridge;
    var sysinfo = B.execSync(_BARCODE, "getSysInfo", []);
    console.log("当前系统信息：" + sysinfo);
    if (sysinfo.indexOf("rk3288") >= 0) {
        if (sysinfo.indexOf("yx_rk3288") >= 0) {
            _DevCODE = 'devapi_yxrk3288';
        } else {
            if (sysinfo.indexOf("smt_rk3288") >= 0) {
                _DevCODE = 'devapi_smt3288';
            } else {
                _DevCODE = 'devapi_rk3288';
            }
        }
    } else if (navigator.userAgent.toLowerCase().indexOf("uniwin") >= 0) {
        _DevCODE = 'devapi_uniwin';
    } else if (navigator.userAgent.toLowerCase().indexOf("rk3128") >= 0) {
        _DevCODE = 'devapi_adw';
    }
    var plugin = {
        setVar_share: function (k, v) {
            return B.execSync(_BARCODE, "setVar_share", [k, v]);
        },
        getVar_share: function (k) {
            return B.execSync(_BARCODE, "getVar_share", [k]);
        },
        getUUID: function () {
            return B.execSync(_BARCODE, "getUUID", []);
        },
        // clearWatchDog: function (successCallback, errorCallback) {
        //     var success = typeof successCallback !== 'function' ? null : function (args) {
        //         successCallback(args);
        //     },
        //         fail = typeof errorCallback !== 'function' ? null : function (code) {
        //             errorCallback(code);
        //         };
        //     callbackID = B.callbackId(success, fail);
        //     var p = BasePage.clearWatchDogTime;
        //     return B.exec(_BARCODE, "clearWatchDog", [callbackID, p]);
        // },
        writeLogFile: function (type, data) {
            return B.exec(_BARCODE, "writeLogFile", [type, data]);
        },
        // openDlgHtml: function (htmlstr) {
        //     return B.exec(_BARCODE, "openDlgHtml", [htmlstr, BasePage.path_www]);
        // },
        startListenSerialPortEvent: function (SerialPortPath) {
            B.exec(_BARCODE, "startListenSerialPortEvent", [SerialPortPath]);
            return document;
        },
        sendSerialPort: function (data) {
            return B.exec(_BARCODE, "sendSerialPort", [data]);
        }
    };
    var devapi = {
        reboot: function () {
            return B.exec(_DevCODE, "reboot", []);
        },
        shutdown: function () {
            return B.exec(_DevCODE, "shutdown", []);
        },
        screenon: function () {
            return B.exec(_DevCODE, "screenon", []);
        },
        screenoff: function () {
            return B.exec(_DevCODE, "screenoff", []);
        },
        openbar: function () {
            return B.exec(_DevCODE, "openbar", []);
        },
        closebar: function () {
            return B.exec(_DevCODE, "closebar", []);
        },
        settime: function (y, m, d, h, mi) {
            return B.exec(_DevCODE, "settime", [y, m, d, h, mi]);
        },
        settimezone: function (timezone) {
            return B.exec(_DevCODE, "settimezone", [timezone]);
        },
        setpoweronoff: function (timeonArray, timeoffArray) {
            return B.exec(_DevCODE, "setpoweronoff", [timeonArray, timeoffArray]);
        },
        setpoweronoff_byweek: function (timeonStr, timeoffStr, weekArray) {
            return B.exec(_DevCODE, "setpoweronoff_byweek", [timeonStr, timeoffStr, weekArray]);
        },
        clearpoweronoff: function () {
            return B.exec(_DevCODE, "clearpoweronoff", []);
        },
        screencap: function () {
            return B.exec(_DevCODE, "screencap", []);
        },
        upgradeApk: function (apkPath) {
            return B.exec(_DevCODE, "upgradeApk", [apkPath]);
        },
        setVoice: function (vol) {
            return B.exec(_DevCODE, "setVoice", [vol]);
        }
    };
    var ttsapi = {
        tts_setpars: function (pars) {
            return B.exec("ttsapi", "tts_setpars", [pars]);
        },
        tts_speak: function (words, pars) {
            return B.exec("ttsapi", "tts_speak", [words, pars]);
        },
        tts_pause: function () {
            return B.exec("ttsapi", "tts_pause", []);
        },
        tts_resume: function () {
            return B.exec("ttsapi", "tts_resume", []);
        },
        tts_stop: function () {
            return B.exec("ttsapi", "tts_stop", []);
        },
        tts_destroy: function () {
            return B.exec("ttsapi", "tts_destroy", []);
        },
        tts_setwhocloud: function (index) {
            return B.exec("ttsapi", "tts_setwhocloud", [index]);
        },
        tts_setwholocal: function (index) {
            return B.exec("ttsapi", "tts_setwholocal", [index]);
        },
        tts_setEngineType: function (engType) {
            return B.exec("ttsapi", "tts_setEngineType", [engType]);
        }
    }
    window.plus.pluginApp = plugin;
    window.plus.devapi = devapi;
    window.plus.ttsapi = ttsapi;
}