import { request } from "../utils";
export default {
	//获取缴费项目
	GetPayments: async ({postData}) => {return await request.post("/api/SelfPay/GetPayments", postData,"");},
	//自助机缴费登记接口
	PaymentRegister: async ({postData}) => {return await request.post("/api/SelfPay/PaymentRegister", postData,"");},
	//缴费确认接口
	PaymentConfirm: async ({postData}) => {return await request.post("/api/SelfPay/PaymentConfirm", postData,"");},
};