import axios from "axios";
import { MD5 } from "crypto-js";
import store from "../store";
import router from "../router";

let showLoading = (tip) => {
  store._actions.showLoading[0](tip);
};
let hideLoading = () => {
  console.log(store._actions)
  store._actions.hideLoading[0]();

};
let showAlertDialog = (msg) => {
  store._actions.showAlertDialog[0](msg);
};

const options = {
  url: "",
  method: "get", // default
  baseURL: "",
  transformRequest: [
    function (data) {
      // Do whatever you want to transform the data

      return JSON.stringify(data);
    },
  ],

  // `transformResponse` allows changes to the response data to be made before
  // it is passed to then/catch
  transformResponse: [
    function (data) {
      // Do whatever you want to transform the data

      return data;
    },
  ],
  // `headers` are custom headers to be sent
  headers: { "Content-Type": "application/json" },

  // `params` are the URL parameters to be sent with the request
  // Must be a plain object or a URLSearchParams object
  params: {},
  data: {},
  // `responseType` indicates the type of data that the server will respond with
  // options are: 'arraybuffer', 'document', 'json', 'text', 'stream'
  //   browser only: 'blob'
  responseType: "json", // default

  // `responseEncoding` indicates encoding to use for decoding responses
  // Note: Ignored for `responseType` of 'stream' or client-side requests
  responseEncoding: "utf8", // default
};

const getHeaders = (data) => {
  const staffId = window.config.StaffId; //当前请求用户StaffId
  const timeStamp = new Date().getTime(); //发起请求时的时间戳（单位：毫秒）
  const nonce = Math.floor(Math.random() * 10000); //随机数
  const sjson = JSON.stringify(data, Object.keys(data).sort()).replace(
    /\[|:|{|}|"|,|\]/g,
    ""
  );
  var signStr = timeStamp + "" + nonce + staffId + localStorage.token + sjson;
  const signature = MD5(signStr)
    .toString()
    .toUpperCase();
  return {
    staffId,
    timeStamp,
    nonce,
    signature,
  };
};

export default {
  async get(url, params, tip, noShowLoading, appendHeader = 1) {
    try {
      !noShowLoading && showLoading(tip);
      let _option = {
        url,
        params: params,
        headers: Object.assign(
          {},
          options.headers,
          appendHeader ? getHeaders(params) : {}
        ),
        baseURL: window.config.baseURL
      };
      let result = await (await axios(Object.assign({}, options, _option)))
        .data;
      console.log(`get请求：地址（${url}）；参数（${JSON.stringify(params)}），；结果（${JSON.stringify(result)}）`)
      !noShowLoading && hideLoading();

      if (result.StatusCode && result.StatusCode != 200) {
        if (result.StatusCode == 403) {
          await store._actions.GetToken[0]()
          return this.post(url, params, tip, noShowLoading, appendHeader)
        } else {
          showAlertDialog({
            msg: result.Info + `（${result.Data}）`,
            successEvent() {
              router.replace("/home")
              window.location.reload()
            },
            timeSpan: 60
          })
        }

      } else {
        return result;
      }
    } catch (error) {
      !noShowLoading && hideLoading();
      showAlertDialog(error.message);
    }
  },
  async post(url, data, tip, noShowLoading, appendHeader = 1) {
    try {
      !noShowLoading && showLoading(tip);
      let _option = {
        url,
        data,
        method: "post",
        headers: Object.assign(
          {},
          options.headers,
          appendHeader ? getHeaders(data) : {}
        ),
        baseURL: window.config.baseURL
      };
      let result = await (await axios(Object.assign({}, options, _option)))
        .data;
      console.log(`post请求：地址（${url}）；参数（${JSON.stringify(data)}），；结果（${JSON.stringify(result)}）`)

      !noShowLoading && hideLoading();

      if (result.StatusCode && result.StatusCode != 200) {
        if (result.StatusCode == 403) {
          await store._actions.GetToken[0]()
          return this.post(url, data, tip, noShowLoading, appendHeader)
        } else {
          showAlertDialog({
            msg: result.Info,
            successEvent() {
              router.replace("/home")
              window.location.reload()
            }
          })
        }

      } else {
        return result;
      }
    } catch (error) {
      !noShowLoading && hideLoading();
      showAlertDialog(error.message);
    }
  },
  async myPost(url, data, tip, noShowLoading) {
    try {
      !noShowLoading && showLoading(tip);
      let _option = {
        url,
        data,
        method: "post",
      };
      let result = await (await axios(Object.assign({}, options, _option)));
      console.log(`myPost请求：地址（${url}）；参数（${JSON.stringify(data)}），；结果（${JSON.stringify(result)}）`)
      !noShowLoading && hideLoading();
      return result.data;
    } catch (error) {
      !noShowLoading && hideLoading();
      showAlertDialog(error.message);
    }
  }
};
