export default {
  others: {
    getSDCardPath() {
      return 'D:';
    },
    get_signfilePath() {
      return 'D:/windor_sign.ini';
    },
    keyfile() {
      let plus = window.plus
      var exePath = plus.others.getExeRootPath()
      var ret = exePath + '/www/license/key.ini';
      return ret;
    },
    writeLogSys(data) {
      let plus = window.plus
      try {
        plus.others.writeLog(data)
      } catch (e) {
        //
      }
    }
  },
  io: {
    PRIVATE_DOC: '',
    requestFileSystem: function (doc, onInitFs) {
      window.requestFileSystem(window.PERSISTENT, //WINDOW.PERSISTENT(永久) , or TEMPORARY(临时)
        1024 * 1024 * 4000, //1M  
        function onsuccess(fs) {
          fs.root.getDirectory('yd_documents', {
            create: true
          }, function (dirEntry2) {
            console.log('You have just created the ' + dirEntry2.name + ' directory.');
            onInitFs(fs);
          },
            function (err) {
              var msg = 'An error occured: ';
              console.log(msg + err);
            }
          );
        }

        , //成功后的回调函数 
        function (err) {
          var msg = 'An error occured: ';
          console.log(msg + err);
        }
      ); //错误后的回调函数
    },
    resolveLocalFileSystemURL: function (filename, suc, err) {
      let plus = window.plus;
      let result = plus.others.readTxtFile(filename)
      if (result) {
        suc && suc(result)
      } else {
        err && err(result)
      }
    }
    ,
    convertLocalFileSystemURL: function (path) {//相对路径转绝对路径
      return path;
    }
  },
}