<template>
  <v-app class="page_gs">
    <router-view></router-view>
    <heroBackgound
      :isBlur="$router.currentRoute.path!='/home'"
      style="width:100%;height:100%;position:absolute"
    ></heroBackgound>
    <v-overlay opacity="0" z-index="210" style="color:#000000" v-show="$store.state.loading.state">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
      <div
        class="h-center-level"
        style="margin-top: 10px;width: 100vw;text-align: center;"
      >{{$store.state.loading.text?(`正在${$store.state.loading.text}...`):`加载中...`}}</div>
    </v-overlay>
    <v-dialog v-model="$store.state.alertDialog.state" persistent max-width="290">
      <v-card>
        <v-card-title class="headline">温馨提示！</v-card-title>
        <v-card-text>{{$store.state.alertDialog.msg}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="hideAlertDialog();">确定</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <div
      class="h-center"
      :style="{'max-width':'290px',
    'transform-origin': 'center center',
    'box-shadow':'0 3px 1px -2px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 5px 0 rgba(0,0,0,.12)'}"
    >
      <v-card>
        <v-card-title class="headline">配置web地址</v-card-title>
        <v-list>
          <v-list-item>
            <div>IP：</div>
            <v-text-field v-model="url" type="text" placeholder="192.168.1.1" label></v-text-field>
          </v-list-item>
          <v-list-item>
            <div>端口：</div>
            <v-text-field v-model="port" placeholder="90" type="text" label></v-text-field>
          </v-list-item>
        </v-list>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="enter">确认</v-btn>
        </v-card-actions>
      </v-card>
    </div>
  </v-app>
</template>

<script>
import { mapActions, mapState } from "vuex";
import heroBackgound from "../components/theme/heroBackgound";
import windowsPlusEx from "../natives/windowsPlus";
import androidPlusEx from "../natives/androidPlus";
import { initPluginApp } from "../natives/androidPlus";
import { time } from "../utils";

let newTime = new Date().getTime();
export default {
  data() {
    return {
      url: "",
      port: "",
      dialog: true,
      plusExtend: null,
    };
  },
  beforeCreate() {},
  async created() {
    if (window.oldLog) {
      window.oldLog("urlHub");
    }
    let _this = this;
    _this.showLoading();
    this.url = localStorage.myUrl;
    this.port = localStorage.myPort;
    await this.loadPlatform();
    if (await this.initPlus()) {
      Object.assign(this.plusExtend.others, window.plus.others);
      Object.assign(window.plus, this.plusExtend);
    }

    if (this.url && this.port) {
      let _url = this.url + ":" + this.port;
      _url = _url.startsWith("http") ? _url : "http://" + _url;

      let doit = async function () {
        let valires = window.plus.others.yDHZWebsiteValid(_url);
        _this.hideLoading();
        if (valires == "") {
          window.location.replace(_url + "?v=" + newTime);
          return;
        }
        await time.delaly(
          (Number(window.config.checkWebisValidTimeSpan) || 5) * 1000
        );
        doit();
      };
      doit();
    } else {
      _this.hideLoading();
    }
  },
  computed: {
    ...mapState(["Platform"]),
  },
  watch: {},
  components: { heroBackgound },
  methods: {
    loadPlatform() {
      let _this = this;
      return new Promise((resolve) => {
        let ua = window.navigator.userAgent;
        let android = ua.match(/Android|Adr/);
        if (window.CefSharp && !android) {
          _this.setPlatform("Windows");
          _this.plusExtend = windowsPlusEx;
          return resolve();
        } else if (android) {
          _this.setPlatform("Android");
          _this.plusExtend = androidPlusEx;
          return resolve();
        }
        return resolve();
      });
    },
    initKeyboardEvent() {
      let _this = this;
      document.addEventListener(
        "keydown",
        (event) => {
          const keyName = event.key;
          if (keyName == "Enter") {
            _this.enter();
          }
        },
        false
      );
    },
    async initPlus() {
      let _this = this;
      if (_this.Platform == "Android") {
        await new Promise((resolve) => {
          document.onreadystatechange = function () {
            if (document.readyState === "interactive") {
              if (window.plus) {
                initPluginApp();
                return resolve();
              } else {
                document.addEventListener(
                  "plusready",
                  () => {
                    initPluginApp();
                    return resolve();
                  },
                  false
                );
              }
            }
          };
        });
        return !0;
      } else if (_this.Platform == "Windows") {
        await _this.initCefSharp();
        return !0;
      }
      return !1;
    },
    ...mapActions([
      "showAlertDialog",
      "hideAlertDialog",
      "setPlatform",
      "setCefSharpState",
      "showLoading",
      "hideLoading",
    ]),
    async initCefSharp() {
      let _this = this;
      let CefSharp = window.CefSharp;
      if (!CefSharp) return;
      let result = await CefSharp.BindObjectAsync("plus");
      if (result.Success) {
        _this.setCefSharpState(!0);
      }
    },
    enter() {
      let _this = this;
      _this.showLoading("验证web地址");
      setTimeout(() => {
        let _url = this.url + ":" + this.port;
        if (this.validURL(_url)) {
          _url = _url.startsWith("http") ? _url : "http://" + _url;

          try {
            new URL(_url);
            let valires = window.plus.others.yDHZWebsiteValid(_url);
            _this.hideLoading();
            if (valires == "") {
              localStorage.myUrl = this.url;
              localStorage.myPort = this.port;
              window.location.replace(_url + "?v=" + newTime);
            } else {
              this.showAlertDialog(valires);
            }
          } catch (e) {
            _this.hideLoading();
            console.error(e);
            this.showAlertDialog("请输入正确的URL地址");
          }
        } else {
          _this.hideLoading();
          this.showAlertDialog("请输入正确的URL地址1");
        }
      }, 100);
    },
    validURL(str) {
      let pattern = new RegExp(
        "^(https?:\\/\\/)?" + // protocol
        "((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|" + // domain name
        "((\\d{1,3}\\.){3}\\d{1,3}))" + // OR ip (v4) address
        "(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*" + // port and path
        "(\\?[;&a-z\\d%_.~+=-]*)?" + // query string
          "(\\#[-a-z\\d_]*)?$",
        "i"
      ); // fragment locator
      return !!pattern.test(str);
    },
  },
};
</script>

<style scoped>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
}
</style>