import { hardwareApi, SelfPayICMENU } from "../api";
import { MD5 } from "crypto-js";
import { MyDESEncrypt } from "../utils";
import router from "../router";
let timeOutBackItervid;
let alertDialogTimeoutid;
export default {
  starTimeOutBack({ commit, state }) {
    timeOutBackItervid && clearInterval(timeOutBackItervid);
    commit("setTimeOutBackSpanInit");
    timeOutBackItervid = setInterval(() => {
      commit("setTimeOutBackSpan", Number(state.TimeOutBackSpan) - 1);
      if (state.TimeOutBackSpan <= 0) {
        clearInterval(timeOutBackItervid);
        state.readCardLoaderDialog = false;
        router.push("/home");
        timeOutBackItervid = undefined;
        commit("setTimeOutBackSpanInit");
      }
    }, 1000);
  },
  setTimeOutBackSpanInit({ commit }) {
    commit("setTimeOutBackSpanInit");
  },
  setTimeOutBackSpan({ commit }, d) {
    commit("setTimeOutBackSpan", d);
  },
  endTimeOutBack({ commit }) {
    timeOutBackItervid && clearInterval(timeOutBackItervid);
    commit("setTimeOutBackSpanInit");
    commit("hideAlertDialog");
  },
  restarTimeOutBack({ commit }) {
    commit("setTimeOutBackSpanInit");
  },
  hideAlertDialog({ commit }) {
    commit("hideAlertDialog", 1);
  },
  showAlertDialog({ commit }, d) {
    commit("showAlertDialog", d);
    let timeSpan = window.config.showAlertDialogTimeSpan || 15
    if (typeof d == "object" && d.timeSpan) timeSpan = d.timeSpan
    alertDialogTimeoutid && clearTimeout(alertDialogTimeoutid)
    alertDialogTimeoutid = setTimeout(() => {
      commit("hideAlertDialog");
      alertDialogTimeoutid = 0
    }, timeSpan * 1000);
  },
  showReadCardLoaderDialog({ commit }) {
    commit("showReadCardLoaderDialog");
  },
  hideReadCardLoaderDialog({ commit }) {
    commit("hideReadCardLoaderDialog");
  },
  showLoading({ commit }, d) {
    d = d ? d.split("|") : ["", ""]
    let text = d[0]
    let color = d[1]
    commit("switchLoading", {
      state: !0,
      text,
      color,
    });
  },
  hideLoading({ commit }) {
    commit("switchLoading", {
      state: !1,
      text: ""
    });
  },
  async updateMenuList({ commit, state }) {
    //commit("updateMenuList", d);
    await SelfPayICMENU(
      {
        user_id: state.curCardInfo.cardData.userID,
        org_id: window.config.orgid
      },
      (d) => {
        console.log(d.sJson[0].child)
        commit("updateMenuList", d.sJson[0].child);
      },
      () => {
        commit("showAlertDialog", "获取菜单失败！");
      }
    );
  },
  showChildMenuList({ commit }, menuList) {
    router.push("/menu");
    commit("showChildMenuList", menuList);
  },
  async GetToken({ commit }) {
    const key = window.config.StaffPaswd;
    const datetime = new Date().yyyyMMddHHmmss();
    const tokenType = "query";
    const mac = MD5(key + datetime + tokenType)
      .toString()
      .toUpperCase();
    const data = {
      staffId: window.config.StaffId,
      datetime,
      mac,
      tokenType,
    };
    let bl = false;
    await hardwareApi.GetToken(data, (d) => {
      const KEY = window.config.StaffPaswd.substring(0, 8);
      var decryted_content = MyDESEncrypt.ydDecryptDes(d.Data.temp, KEY);
      console.log(MyDESEncrypt.ydencryptDes("888888", KEY));
      console.log(decryted_content);
      localStorage.token = decryted_content;
      commit("SetToken", d);
      bl = true;
    });
    return bl
  },
  setCurCardInfo({ commit }, d) {
    commit("setCurCardInfo", d);
  },
  showConfirmDialog({ commit }, { msg, successEvent, cancelEvent }) {
    commit("showConfirmDialog", { msg, successEvent, cancelEvent });
  },
  hideConfirmDialog({ commit, state }, yes) {
    if (yes)
      state.confirmDialog.successEvent && state.confirmDialog.successEvent();
    else state.confirmDialog.cancelEvent && state.confirmDialog.cancelEvent();

    commit("setConfirmDialog", {
      state: false,
      msg: "",
      successEvent: null,
      cancelEvent: null,
    });
  },
  setCurIsNeedIdentity({ commit }, d) {
    commit("setCurIsNeedIdentity", d);
  },
  setReader5018Ins({ commit }, d) {
    commit("setReader5018Ins", d);
  },
  setUserInfo({ commit }, d) {
    commit("setUserInfo", d);
  },
  setConfiged({ commit }, d) {
    commit("setConfiged", d)
  },
  setCefSharpState({ commit }, d) {
    commit("setCefSharpState", d)
  },
  setPlatform({ commit }, d) {
    commit("setPlatform", d)
  },
  setUUID({ commit }, d) {
    commit("setUUID", d)
  },
  setDebug({ commit }, d) {
    commit("setDebug", d)
  },
  setNewTimeOutBackSpan({ commit }, d) {
    commit("setNewTimeOutBackSpan", d)
  },
};
