let module = {
  namespaced: true,

  // 模块内容（module assets）
  state: {
    img:"head.png"
  }, 
  getters: {
    isAdmin() {} // -> getters['account/isAdmin']
  },
  actions: {
    login() {} // -> dispatch('account/login')
  },
  mutations: {
    login() {} // -> commit('account/login')
  },

  // 嵌套模块
  modules: {
    // 继承父模块的命名空间
    myPage: {
      state: {},
      getters: {
        profile() {} // -> getters['account/profile']
      }
    },
    // 进一步嵌套命名空间
    posts: {
      namespaced: true,
      state: {},
      getters: {
        popular() {} // -> getters['account/posts/popular']
      }
    }
  }
};
export default module;
