export const hardwareApi = {
  GetCardIniParam: async (data, successCallback, errorCallback) => {
    let result;
    await request.post("/api/SelfPay/GetCardIniParam", data, "初始化读卡参数").then(
      (d) => {
        successCallback && successCallback(d);
        result = d;
      },
      (d) => {
        errorCallback && errorCallback(d);
      }
    );
    return result;
  },
  GetMacVerify: async (data, successCallback, errorCallback) => {
    let result;
    await request.post("/api/SelfPay/GetMacVerify", data, "MAC验证").then(
      (d) => {
        successCallback && successCallback(d);
        result = d;
      },
      (d) => {
        errorCallback && errorCallback(d);
      }
    );
    return result;
  },
  GetToken: async (data, successCallback, errorCallback) => {
    return await request.get("/api/Service/GetToken", data, "获取token").then(
      (d) => {
        successCallback && successCallback(d);
      },
      (d) => {
        errorCallback && errorCallback(d);
      }
    );
  },
};

export const base = {
  async GetOrgID() {
    let result
    await request.post("/api/SelfPay/GetOrgID", {
      enterprise_id: window.config.enterprise_id
    }).then(
      (d) => {
        result = d
      }
    );
    return result
  },
  async getTermitalConfig() {
    let result
    await request.get("/api/SmartTerminal/GetTermParamDef", {
      enterprise_id: window.config.enterprise_id,
      TermId: window.config.TermId
    }).then(
      (d) => {
        result = d
      }
    );
    return result
  }
};
export { shuikong }
export const configData = {
  async GetOrgList() {
    let result;
    await request.post("/api/SelfPay/GetOrgList", {},"",true).then(
      (d) => {
        result = d
      }
    );
    return result
  },
  async GetterminalList(org_id) {
    let result;
    await request.post("/api/SelfPay/GetterminalList", {
      org_id
    }).then(
      (d) => {
        result = d
      }
    );
    return result
  },
  async GetAgencyList(org_id) {
    return await request.post("/api/SelfPay/GetAgencyList", {
      org_id
    })
  }
}
export const registerApi = {
  baseUrl: "http://windor-wisdom.com:8000/WindorRegisterWebApi/api",
  async checkRegister(DevCode) {
    return await request.myPost(this.baseUrl + "/STRegister/CheckRegister", {
      DevCode
    }, "验证注册")
  }
}