Date.prototype.getMonthList = function () {
  let monthList = [];
  for (let i = 0; i < 5; i++) {
    let now = new Date();
    monthList.push(
      new Date(now.setMonth(now.getMonth() - i)).getMonth() + 1 + "月"
    );
  }
  return monthList;
};
Date.prototype.yyyy_MM_ddHHmmss = function (customTime) {
  let MM = this.getMonth() + 1;
  let dd = this.getDate();
  let HH = this.getHours();
  let mm = this.getMinutes();
  let ss = this.getSeconds();
  let newDate = [
    this.getFullYear() + "-",
    (MM > 9 ? "" : "0") + MM + "-",
    (dd > 9 ? "" : "0") + dd + " ",
  ];
  if (!customTime) {
    newDate = newDate.concat([
      (HH > 9 ? "" : "0") + HH + ":",
      (mm > 9 ? "" : "0") + mm + ":",
      (ss > 9 ? "" : "0") + ss,
    ]);
  } else {
    newDate.push(customTime);
  }
  return newDate.join("");
};
Date.prototype.yyyy_MM_dd = function () {
  let MM = this.getMonth() + 1;
  let dd = this.getDate();
  let newDate = [
    this.getFullYear() + "-",
    (MM > 9 ? "" : "0") + MM + "-",
    (dd > 9 ? "" : "0") + dd + " ",
  ];
  return newDate.join("");
};

Date.prototype.showTimeWithChiness = function () {
  let MM = this.getMonth() + 1;
  let dd = this.getDate();
  let HH = this.getHours();
  let mm = this.getMinutes();
  let ss = this.getSeconds();
  // let ff = this.getMilliseconds();
  return [
    this.getFullYear() + "年",
    (MM > 9 ? "" : "0") + MM + "月",
    (dd > 9 ? "" : "0") + dd + "日 ",
    (HH > 9 ? "" : "0") + HH + ":",
    (mm > 9 ? "" : "0") + mm + ":",
    (ss > 9 ? "" : "0") + ss,
  ].join("");
};
Date.prototype.yyyyMM = function (splitString) {
  let MM = this.getMonth() + 1;
  return [
    this.getFullYear(),
    splitString || "-",
    (MM > 9 ? "" : "0") + MM,
  ].join("");
};
Date.prototype.yyyyMMddHHmmss = function () {
  let MM = this.getMonth() + 1;
  let dd = this.getDate();
  let HH = this.getHours();
  let mm = this.getMinutes();
  let ss = this.getSeconds();
  return [
    this.getFullYear(),
    (MM > 9 ? "" : "0") + MM,
    (dd > 9 ? "" : "0") + dd,
    (HH > 9 ? "" : "0") + HH,
    (mm > 9 ? "" : "0") + mm,
    (ss > 9 ? "" : "0") + ss,
  ].join("");
};
Date.prototype.yyyyMMddHHmmssff = function () {
  let MM = this.getMonth() + 1;
  let dd = this.getDate();
  let HH = this.getHours();
  let mm = this.getMinutes();
  let ss = this.getSeconds();
  let ff = this.getMilliseconds();
  return [
    this.getFullYear(),
    (MM > 9 ? "" : "0") + MM,
    (dd > 9 ? "" : "0") + dd,
    (HH > 9 ? "" : "0") + HH,
    (mm > 9 ? "" : "0") + mm,
    (ss > 9 ? "" : "0") + ss,
    (ff > 9 ? "" : "0") + ff,
  ].join("");
};
export default {
  delaly(duration) {
    let id
    return new Promise((resolve) => id = setTimeout(() => { resolve(id) }, duration));
  },
};
